html,
body {
  height: 100%;
}
#__next {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: calc(100% - 70px);
  padding: 10px;
}
#__next > .convertor {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
#__next > .convertor > section {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
#__next > .convertor > section + section {
  margin-left: 10px;
}
#__next > .convertor > section > textarea {
  width: 100%;
  height: 100%;
}
#__next > .convertor > section.target > div {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: baseline;
  -webkit-align-items: baseline;
          align-items: baseline;
}
#__next > .convertor > section.target > div > h3 {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
